@use '../util' as u;

.promo {

  &__boxes {
    display: flex;
    flex-direction: row;
    gap: u.rem(32);
    margin-bottom: u.rem(80);

    @include u.breakpoint-down(medium) {
      flex-direction: column;
      align-items: center;
    }
  }
}

.savingProgram {

  &__priceDrop {
    background-image: url('/img/savingPromo_price_drop_bkgd_1920x400.jpg');
    // height: auto;
    // width: 100%;
    // object-fit: cover;
  }

  &__fullWidth {
    width: 100%;
    height: auto;
    object-fit: cover;

    @include u.breakpoint-down(small) {
      height: u.rem(300);
    }
  }
}

.inStore {

  &__promo {

    margin-bottom: u.rem(80);
  }
}

.advocacy {
  background-color: var(--advocacyRed);
  margin-bottom: u.rem(80);

  &__text {
    font-size: 90px;
    line-height: 1;
    color: var(--white);
    margin-bottom: 0;
    display: block;

    h2 {
      font-size: u.rem(40);
      font-weight: 700;
      margin-bottom: u.rem(20);
    }

    h3 {
      font-size: u.rem(24);
      font-weight: 700;
      margin-bottom: u.rem(12);
    }
  }
}

.site {

  &__cards {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    justify-content: center;
    gap: u.rem(32);
    margin-bottom: u.rem(80);
    padding: 0;

    @include u.breakpoint-down(small) {
      flex-direction: column;
      gap: 0;
    }

    p,
    ul {
      margin-bottom: 24px;
    }

  }

  .col1 {
    @include u.breakpoint-down(small) {
      margin-bottom: u.rem(80);
    }
  }

  .col1,
  .col2 {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    width: 100%;
    }
  

  &__img {
    margin-bottom: 24px;
  }

  &__text {
    padding: 0 24px;

    &--callout {
      color: var(--primaryBlue);
      font-weight: 700;
    }
  }
}

.hhf {

  &__textLink {
    color: var(--darkBlue);

    &:hover {
      color: var(--primaryOrange);
    }
  }
}

.we {

  &__cardList ul li {
    margin-bottom: 20px;
  }

  &__cardList li {
    margin-bottom: 16px;
  }
}

.toc {
  margin-bottom: u.rem(80);

  &__links {
    color: var(--primaryBlue);
    font-weight: 700;
    padding: 1rem 0 0 1.25rem;

    h3 {
      font-size: u.rem(32);
      font-weight: 700;
    }

    a {
      color: var(--primaryBlue);
      font-size: u.rem(20);
      font-weight: 500;
      text-decoration: underline;
      text-underline-offset: 6px;
      text-decoration-thickness: 2px;
      text-decoration-color: var(--primaryBlue);

      &:hover {
        color: var(--primaryOrange);
        text-decoration: none;
      }
    }
  }

  &__row {
    display: grid;
    grid-template-columns: repeat(3, 1fr);



    @include u.breakpoint-down(medium) {
      grid-template-columns: repeat(2, 1fr);
    }

    @include u.breakpoint-down(small) {
      grid-template-columns: 1fr;
    }

    &--links {
      margin-bottom: u.rem(48);
    }
  }
}

.community {

  &__intro {
    background-color: var(--primaryBlue);
    color: var(--white);
    margin-bottom: u.rem(80);
    }

    &__textIntro {
      @include u.breakpoint-down(small) {
        padding-top: 40px;
      }
  }

  &__content {
    margin-bottom: u.rem(80);

    h2 {
      font-size: u.rem(40);
      font-size: clamp(u.rem(40), u.rem(35) + 1vw, u.rem(48));
      font-weight: 700;
      color: var(--primaryBlue);
      margin-bottom: u.rem(24);
    }

    &--rows {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      padding: 0 u.rem(24);
      gap: u.rem(16);

      @include u.breakpoint-down(small) {
        grid-template-columns: 100%;
        padding: 0;
      }
    }

  }
}

.hhf {

  &__intro {
    margin-bottom: u.rem(80);
  }

  &__textIntro {

    // h3 {
    //   color: var(--darkGreen);
    // }
  }

  &-subhead {
    margin-top: u.rem(48);
  }

  &__bannerImg {
    margin-bottom: u.rem(80);
  }

  &__runNorth {
    background-color: var(--primaryGreen);
    margin-bottom: u.rem(80);
  }
}

.subpage {
  
  &__textIntro {

  h3 {
    font-size: u.rem(32);
    font-size: clamp(u.rem(32), u.rem(27) + 1vw, u.rem(40));
    font-weight: 700;
  }

  .hhf__subhead {
    color: var(--darkGreen);
  }

  .ncr__subhead {
    color: var(--primaryBlue);
  }

  }

  &__ncrCol1 {
    margin-bottom: u.rem(48);
  }

  &__ncrCol2 {
    // background-color: var(--lightBlue);
    // padding: u.rem(24);
  }
}